import { 
  collection, 
  getDocs, 
  getDoc,
  doc,
  query, 
  where, 
  orderBy,
  DocumentData,
  QuerySnapshot,
  onSnapshot,
  Unsubscribe
} from 'firebase/firestore';
import { db } from './config';
import { collectionNamesToCheck, createCategoryFromCollectionName } from './categories-config';

export interface Product {
  id: string;
  name: string;
  description: string;
  categoryId: string;
  imageUrl?: string;
  price?: number;
  inStock?: boolean;
  createdAt?: any;
}

export interface Category {
  id: string;
  name: string;
  icon: string;
  gradient: string;
  description?: string; // Optional description for category
  order?: number; // Optional order field for sorting
  collectionName?: string; // Name of the Firestore collection for this category (e.g., "Electronics")
}

/**
 * Check if error is a permission denied error
 */
function isPermissionError(error: any): boolean {
  return error?.code === 'permission-denied' || 
         error?.code === 7 || 
         error?.message?.toLowerCase().includes('permission') ||
         error?.message?.toLowerCase().includes('missing or insufficient');
}

/**
 * Fetch all products from all category collections in Firestore
 * This aggregates products from all category-specific collections (e.g., "Electronics", "Sweets", etc.)
 * 
 * FIX: "All Products not showing all items" - This function now properly aggregates products
 * from all category collections instead of looking for a single 'products' collection
 */
export async function getAllProducts(): Promise<Product[]> {
  try {
    console.log('üîÑ Fetching ALL products from all category collections...');
    
    // First, get all categories to know which collections to fetch from
    const categories = await getAllCategories();
    
    if (categories.length === 0) {
      console.warn('‚ö†Ô∏è No categories found. Cannot fetch products without categories.');
      return [];
    }
    
    console.log(`üìã Found ${categories.length} categories. Fetching products from each collection...`);
    console.log(`üìã Category to Collection mapping:`, categories.map(cat => ({
      categoryName: cat.name,
      categoryId: cat.id,
      collectionName: cat.collectionName || cat.name
    })));
    
    const allProducts: Product[] = [];
    const collectionNames = categories.map(cat => cat.collectionName || cat.name);
    
    // Log all collections that will be checked
    console.log(`üì¶ Collections to fetch from:`, collectionNames);
    
    // Fetch products from each category collection in parallel
    const fetchPromises = collectionNames.map(async (collectionName) => {
      try {
        console.log(`  üì¶ Fetching from collection: "${collectionName}"`);
        const productsRef = collection(db, collectionName);
        
        // Try to fetch with orderBy first
        try {
    const q = query(productsRef, orderBy('createdAt', 'desc'));
          const querySnapshot = await getDocs(q);
          
          const categoryProducts: Product[] = [];
          querySnapshot.forEach((doc) => {
            const data = doc.data();
            // Ensure categoryId is set from the collection name or category ID
            const category = categories.find(cat => 
              (cat.collectionName || cat.name) === collectionName
            );
            
            categoryProducts.push({
              id: doc.id,
              categoryId: category?.id || collectionName.toLowerCase().replace(/[^a-z0-9]+/g, '-'),
              ...data,
            } as Product);
          });
          
          console.log(`  ‚úÖ Fetched ${categoryProducts.length} products from "${collectionName}"`);
          return categoryProducts;
        } catch (orderByError: any) {
          // If orderBy fails, try without it
          if (orderByError?.code === 'failed-precondition' || isPermissionError(orderByError)) {
            const querySnapshot = await getDocs(productsRef);
            
            const categoryProducts: Product[] = [];
    querySnapshot.forEach((doc) => {
              const data = doc.data();
              const category = categories.find(cat => 
                (cat.collectionName || cat.name) === collectionName
              );
              
              categoryProducts.push({
        id: doc.id,
                categoryId: category?.id || collectionName.toLowerCase().replace(/[^a-z0-9]+/g, '-'),
                ...data,
      } as Product);
    });
    
            // Sort client-side
            categoryProducts.sort((a, b) => {
              const aDate = a.createdAt?.toMillis?.() || 0;
              const bDate = b.createdAt?.toMillis?.() || 0;
              return bDate - aDate;
            });
            
            console.log(`  ‚úÖ Fetched ${categoryProducts.length} products from "${collectionName}" (without ordering)`);
            return categoryProducts;
          }
          throw orderByError;
        }
      } catch (error: any) {
        if (isPermissionError(error)) {
          console.warn(`  ‚ö†Ô∏è Permission denied for collection: "${collectionName}"`);
          console.warn(`     Make sure Firestore security rules allow reading from this collection`);
          return [];
        }
        console.error(`  ‚ùå Error fetching from "${collectionName}":`, error);
        console.error(`     Collection might not exist or have a different name`);
        return []; // Return empty array for this collection, continue with others
      }
    });
    
    // Wait for all collections to be fetched
    const results = await Promise.all(fetchPromises);
    
    // Track which collections had products and which didn't
    const collectionSummary: Array<{ name: string; productCount: number }> = [];
    
    // Flatten all products into a single array
    results.forEach((products, index) => {
      const collectionName = collectionNames[index];
      collectionSummary.push({
        name: collectionName,
        productCount: products.length
      });
      allProducts.push(...products);
    });
    
    // Sort all products by createdAt (newest first)
    allProducts.sort((a, b) => {
      const aDate = a.createdAt?.toMillis?.() || 0;
      const bDate = b.createdAt?.toMillis?.() || 0;
      return bDate - aDate;
    });
    
    // Log detailed summary
    console.log(`\nüìä Product Fetch Summary:`);
    console.log(`   Total collections checked: ${collectionNames.length}`);
    console.log(`   Total products fetched: ${allProducts.length}`);
    collectionSummary.forEach(summary => {
      if (summary.productCount === 0) {
        console.warn(`   ‚ö†Ô∏è "${summary.name}": 0 products (collection might be empty or not exist)`);
      } else {
        console.log(`   ‚úÖ "${summary.name}": ${summary.productCount} products`);
      }
    });
    
    // Check for collections with zero products
    const emptyCollections = collectionSummary.filter(s => s.productCount === 0);
    if (emptyCollections.length > 0) {
      console.warn(`\n‚ö†Ô∏è ${emptyCollections.length} collection(s) had 0 products:`);
      emptyCollections.forEach(col => {
        console.warn(`   - "${col.name}"`);
        console.warn(`     Possible reasons:`);
        console.warn(`       1. Collection doesn't exist in Firestore`);
        console.warn(`       2. Collection name in _categories doesn't match exactly`);
        console.warn(`       3. Collection is empty (no product documents)`);
        console.warn(`       4. Permission denied (check Firestore security rules)`);
      });
    }
    
    console.log(`‚úÖ Successfully fetched ${allProducts.length} total products from ${categories.length} categories`);
    
    return allProducts;
  } catch (error: any) {
    console.error('‚ùå Error fetching all products:', error);
    if (isPermissionError(error)) {
      return [];
    }
    return [];
  }
}

/**
 * Fetch products by category
 * Works with Firestore security rules - handles permission errors and query restrictions
 * If category has collectionName, fetches from that collection. Otherwise uses categoryId.
 */
export async function getProductsByCategory(categoryId: string, categoryCollectionName?: string): Promise<Product[]> {
  try {
    if (categoryId === 'all') {
      return await getAllProducts();
    }
    
    // Use collectionName if provided, otherwise default to 'products' collection with categoryId filter
    const collectionName = categoryCollectionName || 'products';
    const productsRef = collection(db, collectionName);
    
    console.log(`\nüîç Fetching products for category:`);
    console.log(`   Category ID: ${categoryId}`);
    console.log(`   Collection Name: "${collectionName}"`);
    console.log(`   Collection Name provided: ${!!categoryCollectionName}`);
    
    if (!categoryCollectionName) {
      console.warn(`‚ö†Ô∏è No collectionName provided for category ${categoryId}`);
      console.warn(`   This means the category document in _categories is missing the 'collectionName' field`);
      console.warn(`   Using default 'products' collection (which may not exist)`);
    }
    
    // Try with orderBy first (if rules allow it and we're using a dedicated collection)
    try {
      const q = categoryCollectionName 
        ? query(productsRef, orderBy('createdAt', 'desc'))
        : query(
      productsRef, 
      where('categoryId', '==', categoryId),
      orderBy('createdAt', 'desc')
    );
    const querySnapshot: QuerySnapshot<DocumentData> = await getDocs(q);
    
    const products: Product[] = [];
    querySnapshot.forEach((doc) => {
      products.push({
        id: doc.id,
          categoryId: categoryId, // Ensure categoryId is set
          ...doc.data(),
        } as Product);
      });
      
      console.log(`‚úÖ Fetched ${products.length} products from collection "${collectionName}"`);
      
      if (products.length === 0) {
        console.warn(`‚ö†Ô∏è No products found in collection "${collectionName}"`);
        console.warn(`   Please verify:`);
        console.warn(`   1. Collection "${collectionName}" exists in Firestore`);
        console.warn(`   2. Collection has product documents`);
        console.warn(`   3. Firestore rules allow reading from this collection`);
        console.warn(`   4. Category's collectionName matches the actual Firestore collection name exactly`);
      }
      
      return products;
    } catch (orderByError: any) {
      // If orderBy fails, try without it
      if (orderByError?.code === 'failed-precondition' || isPermissionError(orderByError)) {
        try {
          const q = categoryCollectionName
            ? query(productsRef)
            : query(productsRef, where('categoryId', '==', categoryId));
          const querySnapshot: QuerySnapshot<DocumentData> = await getDocs(q);
          
          const products: Product[] = [];
          querySnapshot.forEach((doc) => {
            products.push({
              id: doc.id,
              categoryId: categoryId,
        ...doc.data(),
      } as Product);
    });
          
          // Sort client-side as fallback
          products.sort((a, b) => {
            const aDate = a.createdAt?.toMillis?.() || 0;
            const bDate = b.createdAt?.toMillis?.() || 0;
            return bDate - aDate;
          });
          
          console.log(`Fetched ${products.length} products from ${collectionName} (without ordering)`);
          
          if (products.length === 0) {
            console.warn(`‚ö†Ô∏è No products found in collection "${collectionName}"`);
            console.warn(`   Please verify:`);
            console.warn(`   1. Collection "${collectionName}" exists in Firestore`);
            console.warn(`   2. Collection has product documents`);
            console.warn(`   3. Firestore rules allow reading from this collection`);
            console.warn(`   4. Category's collectionName matches the actual Firestore collection name exactly`);
          }
    
    return products;
        } catch (fallbackError: any) {
          console.error(`Error fetching from ${collectionName}:`, fallbackError);
          if (isPermissionError(fallbackError)) {
            return [];
          }
          throw fallbackError;
        }
      }
      throw orderByError;
    }
  } catch (error: any) {
    if (isPermissionError(error)) {
      return [];
    }
    console.error('Error fetching products by category:', error);
    return [];
  }
}

/**
 * Fetch a single product by ID
 * Works with Firestore security rules - handles permission errors
 */
export async function getProductById(productId: string): Promise<Product | null> {
  try {
    const productRef = doc(db, 'products', productId);
    const productSnap = await getDoc(productRef);
    
    if (!productSnap.exists()) {
      return null;
    }
    
    return {
      id: productSnap.id,
      ...productSnap.data(),
    } as Product;
  } catch (error: any) {
    if (isPermissionError(error)) {
      // Permission denied - return null gracefully
      return null;
    }
    console.error('Error fetching product by ID:', error);
    return null;
  }
}

/**
 * Fetch categories directly from _categories collection in Firestore
 * This reads category documents with all their metadata (name, icon, gradient, etc.)
 */
async function getCategoriesFromFirestore(): Promise<Category[]> {
  // Declare categories outside try block so it's accessible in catch
  const categories: Category[] = [];
  const skippedDocs: string[] = [];
  
  try {
    console.log('üîç Fetching categories from _categories collection...');
    const categoriesRef = collection(db, '_categories');
    
    // Query without orderBy first to avoid index issues - we'll sort client-side
    const categoriesQuery = query(categoriesRef);
    const querySnapshot = await getDocs(categoriesQuery);
    
    console.log(`üìä Query returned ${querySnapshot.size} documents from _categories collection`);
    
    // Process each document - use regular loop for better error handling
    const docs = querySnapshot.docs;
    for (let i = 0; i < docs.length; i++) {
      const doc = docs[i];
      try {
        const data = doc.data();
        console.log(`üìÑ Processing document ${doc.id} (${i + 1}/${docs.length}):`, {
          fields: Object.keys(data),
          hasName: !!data.name,
          hasCategoryName: !!data.categoryName,
          hasCollectionName: !!data.collectionName
        });
        
        // Create category from document data
        // Support various field name formats - be flexible
        let categoryName = data.name || data.categoryName || data.title || data.collectionName || doc.id;
        
        // Convert to string and trim
        if (typeof categoryName !== 'string') {
          categoryName = String(categoryName || doc.id);
        }
        categoryName = categoryName.trim();
        
        // Final fallback to document ID if name is empty
        const finalCategoryName = categoryName || doc.id;
        
        // Collection name can be separate or same as category name
        // IMPORTANT: This must match the exact Firestore collection name
        // FIX: Use document ID as collection name when collectionName is missing
        // This handles cases where collection name has underscores (Integrated_Circuits) 
        // but category name has spaces (Integrated Circuits)
        const hasExplicitCollectionName = !!(data.collectionName || data.collection);
        
        // Priority: explicit collectionName > document ID > category name
        // Document ID often matches the actual collection name in Firestore
        // FIX: When no explicit collectionName, prefer document ID over category name
        // because document IDs usually match collection names (e.g., "Integrated_Circuits")
        let finalCollectionName: string;
        if (data.collectionName || data.collection) {
          finalCollectionName = data.collectionName || data.collection || '';
        } else {
          // No explicit collectionName - use document ID as it typically matches the collection name
          finalCollectionName = doc.id || finalCategoryName;
        }
        
        // Log collection name mapping for debugging
        console.log(`   Category: "${finalCategoryName}" ‚Üí Collection: "${finalCollectionName}"`);
        console.log(`   Document ID: "${doc.id}"`);
        
        if (!hasExplicitCollectionName) {
          if (doc.id !== finalCategoryName) {
            console.log(`   ‚ÑπÔ∏è Using document ID "${doc.id}" as collection name (differs from category name "${finalCategoryName}")`);
          } else {
            console.warn(`   ‚ö†Ô∏è No collectionName field found - using category name "${finalCategoryName}" as collection name`);
            console.warn(`   ‚ö†Ô∏è Make sure your Firestore collection is named exactly "${finalCategoryName}" (case-sensitive)`);
            console.warn(`   ‚ö†Ô∏è OR add a "collectionName" field to this document with the exact collection name`);
          }
        }
        
        // Only skip if we truly have no name at all
        if (!finalCategoryName || finalCategoryName.trim() === '') {
          console.warn(`‚ö†Ô∏è Skipping document ${doc.id} - no valid name found. Available fields:`, Object.keys(data));
          skippedDocs.push(doc.id);
          continue;
        }
        
        // Create category ID from name (URL-friendly)
        const categoryId = data.id || finalCategoryName
          .toLowerCase()
          .replace(/[^a-z0-9]+/g, '-')
          .replace(/^-+|-+$/g, '');
        
        // Get gradient with error handling
        let gradient = 'from-gray-900 to-black'; // Default gradient
        if (data.gradient) {
          gradient = data.gradient;
        } else {
          try {
            gradient = getCategoryDefaults(finalCategoryName).gradient;
          } catch (gradientError) {
            console.warn(`Could not get gradient for ${finalCategoryName}, using default`);
          }
        }
        
        // Create category object - use data from document or smart defaults
        const category: Category = {
          id: categoryId,
          name: finalCategoryName,
          collectionName: finalCollectionName,
          icon: data.icon || 'üì¶', // Default icon if not provided
          gradient: gradient,
          description: data.description || `Explore our ${finalCategoryName.toLowerCase()} collection`,
          order: typeof data.order === 'number' ? data.order : 999,
        };
        
        categories.push(category);
        console.log(`‚úÖ Added category: ${finalCategoryName} (id: ${categoryId}, collection: ${finalCollectionName})`);
      } catch (docError: any) {
        const errorMsg = docError?.message || String(docError) || 'Unknown error';
        console.error(`‚ùå Error processing document ${doc.id} (${i + 1}/${docs.length}):`, errorMsg);
        console.error(`   Error details:`, {
          message: docError?.message,
          code: docError?.code,
          name: docError?.name
        });
        skippedDocs.push(doc.id);
        // Continue processing other documents
      }
    }
    
    // Log summary
    try {
      console.log(`\nüìã Category processing summary:`);
      console.log(`   Total documents: ${querySnapshot.size}`);
      console.log(`   Categories created: ${categories.length}`);
      if (skippedDocs.length > 0) {
        console.warn(`   Documents skipped: ${skippedDocs.length}`, skippedDocs);
      }
    } catch (logError) {
      console.warn('Error logging summary, but continuing...', logError);
    }
    
    // Sort by order (client-side) - wrap in try-catch to ensure we return categories even if sorting fails
    try {
      if (categories.length > 0) {
        categories.sort((a, b) => (a.order || 999) - (b.order || 999));
      }
    } catch (sortError) {
      console.warn('Error sorting categories, but returning unsorted categories', sortError);
    }
    
    // FIX: Validate and filter out categories whose collections don't exist or are empty
    if (categories.length > 0) {
      console.log(`\nüîç Validating ${categories.length} categories - checking if collections exist and have products...`);
      
      // Validate each category's collection
      const validationResults = await Promise.all(
        categories.map(async (category) => {
          const collectionName = category.collectionName || category.name;
          const hasProducts = await validateCategoryCollection(collectionName);
          
          return {
            category,
            valid: hasProducts,
            collectionName
          };
        })
      );
      
      // Separate validated and invalid categories
      const validatedCategories: Category[] = [];
      const invalidCategories: Array<{ name: string; collectionName: string; reason: string }> = [];
      
      validationResults.forEach(result => {
        if (result.valid) {
          validatedCategories.push(result.category);
        } else {
          invalidCategories.push({
            name: result.category.name,
            collectionName: result.collectionName,
            reason: 'Collection does not exist or is empty'
          });
        }
      });
      
      // Log validation results
      if (invalidCategories.length > 0) {
        console.log(`\n‚ö†Ô∏è Filtered out ${invalidCategories.length} category/categories with empty or non-existent collections:`);
        invalidCategories.forEach(invalid => {
          console.log(`   - "${invalid.name}" (collection: "${invalid.collectionName}") - ${invalid.reason}`);
        });
        console.log(`   üí° To remove these categories, delete the corresponding documents from _categories collection in Firestore`);
      }
      
      if (validatedCategories.length > 0) {
        console.log(`\n‚úÖ Successfully validated ${validatedCategories.length} categories with products:`, validatedCategories.map(c => `${c.name} (id: ${c.id})`));
        return validatedCategories;
      } else {
        console.warn(`‚ö†Ô∏è All categories were filtered out - no collections exist or have products`);
        return [];
      }
    }
    
    console.warn('‚ö†Ô∏è No categories were created from _categories collection');
    return [];
  } catch (error: any) {
    // Log error details in a readable format
    const errorMessage = error?.message || String(error) || 'Unknown error';
    const errorCode = error?.code || 'NO_CODE';
    const errorName = error?.name || 'Error';
    
    console.error('‚ùå Error fetching from _categories collection:');
    console.error(`   Error: ${errorName}`);
    console.error(`   Message: ${errorMessage}`);
    console.error(`   Code: ${errorCode}`);
    if (error?.stack) {
      console.error(`   Stack:`, error.stack);
    }
    
    if (isPermissionError(error)) {
      console.error('‚ö†Ô∏è Permission denied accessing _categories collection');
    } else {
      console.error('‚ö†Ô∏è Failed to access _categories collection');
    }
    
    // Return any categories that were successfully processed before the error
    if (categories.length > 0) {
      console.log(`‚ö†Ô∏è Returning ${categories.length} categories that were successfully processed before error:`, categories.map(c => c.name));
      // Try to sort them before returning
      try {
        categories.sort((a, b) => (a.order || 999) - (b.order || 999));
      } catch (sortError) {
        console.warn('Could not sort categories, returning unsorted');
      }
    return categories;
    }
    
    return [];
  }
}


/**
 * Validate if a category's collection exists and has at least one product
 * Returns true if collection exists and has products, false otherwise
 */
async function validateCategoryCollection(collectionName: string): Promise<boolean> {
  try {
    const productsRef = collection(db, collectionName);
    // Try to fetch at least one document to check if collection exists and has products
    const querySnapshot = await getDocs(query(productsRef));
    const hasProducts = querySnapshot.size > 0;
    
    if (!hasProducts) {
      console.log(`   üîç Collection "${collectionName}" exists but is empty`);
    }
    
    return hasProducts;
  } catch (error: any) {
    // Collection doesn't exist or permission denied
    if (isPermissionError(error)) {
      console.log(`   üîç Collection "${collectionName}" - permission denied (may not exist)`);
    } else {
      console.log(`   üîç Collection "${collectionName}" does not exist or is inaccessible`);
    }
    return false;
  }
}

/**
 * Get smart defaults for category (for fallback)
 */
function getCategoryDefaults(collectionName: string): { gradient: string } {
  const gradients = [
    'from-gray-900 to-black',
    'from-black to-gray-900',
    'from-gray-800 to-black',
    'from-gray-900 to-gray-800',
    'from-black to-gray-800',
    'from-gray-800 to-gray-900',
    'from-gray-700 to-black',
    'from-black to-gray-700',
  ];
  
  const hash = collectionName.split('').reduce((acc, char) => acc + char.charCodeAt(0), 0);
  const gradient = gradients[hash % gradients.length];
  
  return { gradient };
}

/**
 * Automatically fetch categories from _categories collection in Firestore
 * 
 * Detection method priority:
 * 1. First tries to read from '_categories' collection (automatic from database)
 * 2. Falls back to checking collections listed in categories-config.ts
 * 
 * Categories are fetched directly from the _categories collection - all categories listed there will show up!
 */
export async function getAllCategories(): Promise<Category[]> {
  try {
    console.log(`üîç Starting automatic category detection...`);
    
    // Method 1: Try to get categories directly from _categories collection (automatic from database)
    console.log(`üìã Attempting to fetch categories from _categories collection...`);
    const firestoreCategories = await getCategoriesFromFirestore();
    
    console.log(`üìä Result: Found ${firestoreCategories.length} categories from Firestore`);
    
    if (firestoreCategories.length > 0) {
      console.log(`‚úÖ Using ${firestoreCategories.length} categories from _categories collection in Firestore`);
      console.log(`   Category names:`, firestoreCategories.map(c => c.name).join(', '));
      console.log(`   Category details:`, firestoreCategories.map(c => ({ 
        id: c.id, 
        name: c.name, 
        collectionName: c.collectionName 
      })));
      return firestoreCategories;
    }
    
    console.warn(`‚ö†Ô∏è No categories found in _categories collection, checking if collection exists...`);
    
    // Method 2: Fallback to config file approach (for backward compatibility)
    console.log(`üìù _categories collection not found, using fallback method (checking collections from config)`);
    
    const collectionNamesToCheckList = collectionNamesToCheck;
    
    if (collectionNamesToCheckList.length === 0) {
      console.warn('‚ö†Ô∏è No categories found. Please either:');
      console.warn('   1. Create a "_categories" collection in Firestore with category documents');
      console.warn('   2. Or add collection names to lib/firebase/categories-config.ts');
      return [];
    }
    
    console.log(`Checking ${collectionNamesToCheckList.length} collections...`);
    
    const detectedCategories: Category[] = [];
    
    // Check each collection name to see if it exists and has documents
    for (let i = 0; i < collectionNamesToCheckList.length; i++) {
      const collectionName = collectionNamesToCheckList[i];
      
      try {
        // Try to read from the collection (limit to 1 document for efficiency)
        const collectionRef = collection(db, collectionName);
        const testQuery = query(collectionRef);
        const querySnapshot = await getDocs(testQuery);
        
        // If collection exists and has at least one document, create a category
        if (querySnapshot.size > 0) {
          const category = createCategoryFromCollectionName(collectionName, i + 1);
          detectedCategories.push(category);
          console.log(`‚úì Detected collection: ${collectionName} (${querySnapshot.size} documents)`);
        } else {
          console.log(`‚óã Collection "${collectionName}" exists but is empty`);
        }
      } catch (error: any) {
        if (isPermissionError(error)) {
          console.warn(`‚ö† Permission denied for collection: ${collectionName}`);
          // Still create category - might work when fetching products
          const category = createCategoryFromCollectionName(collectionName, i + 1);
          detectedCategories.push(category);
        } else {
          // Collection doesn't exist or other error - skip it
          console.log(`‚úó Collection "${collectionName}" not found or inaccessible`);
        }
      }
    }
    
    console.log(`\n‚úÖ Auto-detection complete:`);
    console.log(`   Categories detected: ${detectedCategories.length}`);
    if (detectedCategories.length > 0) {
      console.log(`   Category names:`, detectedCategories.map(c => `${c.name} (collection: ${c.collectionName || 'none'})`).join(', '));
    } else {
      console.warn(`   No categories were detected. Please check:`);
      console.warn(`   1. Create "_categories" collection in Firestore with category documents`);
      console.warn(`   2. Or collection names in lib/firebase/categories-config.ts`);
      console.warn(`   3. Firestore security rules allow reading from those collections`);
    }
    
    return detectedCategories;
    
  } catch (error: any) {
    console.error('Error auto-detecting categories:', error);
    return [];
  }
}

/**
 * Apply category filter to products array (client-side filtering)
 * 
 * FIX: "All Products not showing all items" and "Category shows no products"
 * This function ensures clean filtering logic that works correctly
 * 
 * @param allProducts - Complete list of all products
 * @param selectedCategory - Category ID to filter by, or 'all' for all products
 * @param categories - Array of category objects to resolve categoryId from collectionName
 * @returns Filtered products array
 */
export function applyCategoryFilter(
  allProducts: Product[],
  selectedCategory: string,
  categories: Category[]
): Product[] {
  // FIX: "All Products not showing all items" - If "all" is selected, return all products
  if (selectedCategory === 'all') {
    console.log(`üîç Showing all ${allProducts.length} products`);
    return allProducts;
  }
  
  // Find the selected category to get its collectionName and id
  const selectedCategoryData = categories.find(cat => cat.id === selectedCategory);
  
  if (!selectedCategoryData) {
    console.warn(`‚ö†Ô∏è Category "${selectedCategory}" not found in categories list`);
    return [];
  }
  
  // FIX: "Category shows no products" - Improved matching logic
  // Filter products that match the selected category
  const filtered = allProducts.filter(product => {
    // Standardize category field name - check multiple possible field names
    const productCategoryId = product.categoryId || 
                              (product as any).category || 
                              (product as any).Category ||
                              (product as any).catagory; // Handle typo
    
    // Match by category ID (primary match)
    if (productCategoryId === selectedCategory) {
      return true;
    }
    
    // Also check if product's categoryId matches the category's id
    // This handles cases where categoryId was set during getAllProducts()
    if (productCategoryId && selectedCategoryData.id === productCategoryId) {
      return true;
    }
    
    // Match by collection name (fallback for products without categoryId)
    const productCollectionName = (product as any).collectionName;
    const categoryCollectionName = selectedCategoryData.collectionName || selectedCategoryData.name;
    if (productCollectionName === categoryCollectionName) {
      return true;
    }
    
    return false;
  });
  
  console.log(`üîç Filtered ${filtered.length} products for category "${selectedCategoryData.name}" (id: ${selectedCategory})`);
  
  if (filtered.length === 0 && allProducts.length > 0) {
    console.warn(`‚ö†Ô∏è No products matched category "${selectedCategoryData.name}". Checking product category fields...`);
    // Debug: Show sample product category fields
    const sampleProducts = allProducts.slice(0, 3);
    sampleProducts.forEach(p => {
      console.log(`   Sample product: ${p.name}, categoryId: ${p.categoryId}, category: ${(p as any).category}`);
    });
  }
  
  return filtered;
}

